.research-cards-div {
    display: flex;
    flex-direction: column;
  }
  
  .research-heading {
    font-size: 56px;
    font-weight: 400;
  }
  
  .research-container {
    display: flex;
    flex-direction: column;
    width: 90%;
    padding: 20px 10px;
    margin: 0px auto;
    margin-top: 2rem;
  }
  
  .research-container > * {
    flex: 1;
  }
  
  /* Media Query */
  
  @media (max-width: 1380px) {
    .research-heading {
      font-size: 40px;
    }
  }
  
  @media (max-width: 768px) {
    .research-heading {
      font-size: 30px;
      text-align: center;
    }
  }
  