@import "../../_globalColor";

.authorNameList {
  font-size: 18px;
}

.publication {
  margin: none;
  padding: 10px 0px;
  border: none;
  outline: none;
  font-weight: 800;
  font-size: 15px;
}

.publication > p {
  margin: 0px;
}

.research-card {
  padding: 20px;
  margin: 10px 0px;
  width: 100%;
}

.research-text-details {
  margin-bottom: 10px;
}


.research-title-name-duration {
  font-size: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.research-card:hover + .research-card-border {
  border-color: $educationCardBorder;
  width: 100%;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.research-card-border {
  height: 30px;
  width: 80%;
  border-top: 2px solid $lightBorder1;
  margin-bottom: 20px;
  -webkit-transition: width 0.6s ease-in-out;
  -moz-transition: width 0.6s ease-in-out;
  -o-transition: width 0.6s ease-in-out;
  transition: width 0.6s ease-in-out;
}

.research-head {
  display: flex;
  flex-direction: row;
}

.research-text-company {
  margin: 0;
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.research-card-right {
  width: 100%;
}

.research-text-subHeader {
  color: $textColor;
  font-weight: 700;
  font-size: 19px;
  margin: 0px;
  padding-top: 0.8rem;
  line-height: normal;
}

.research-blackborder {
  border: 1px solid black;
  border-radius: 10px;
}

.research-whiteborder {
  border: 1px solid $lightBorder1;
  border-radius: 10px;
}

.research-text-duration {
  color: $titleColor;
  font-size: 16px;
  line-height: 32px;
  margin: 0px;
  padding: 0px;
  font-style: italic;
}

.research-text-desc {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-weight: 800;
}

.research-text {
  margin: 0.5rem 0px 12px 10px;
  text-align: left;
}

.research-description {
  padding: 5px 0px; 
  margin: 0px
}

/* Media Query */

@media (max-width: 1380px) {
  .authorNameList {
    font-size: 16px;
  }
  .inprocessPublication {
    padding: 7px;
    font-weight: 700;
    font-size: 14px;
  }
  .completePublication {
    padding: 7px;
    font-weight: 700;
    font-size: 14px;
  }
  .research-text-duration {
    font-size: 10px;
  }
  .research-title-name-duration {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 14px;
  }
  .research-text-desc {
    font-size: 18px;
    margin: 0.5rem 0px;
    font-weight: 800;
  }
  .research-card-right {
    width: 100%;
  }
  .education-card-border {
    width: 90%;
  }
  .research-text-subHeader {
    padding-top: 0.5rem;
    font-size: 16px;
  }
  .research-text-company {
    font-size: 20px;
    line-height: 32px;
  }
  .research-roundedimg {
    width: 90%;
    height: auto;
    margin-bottom: 0.5rem;
    margin-right: 0px;
  }
}

@media (max-width: 1078px) {
  .authorNameList {
    font-size: 14px;
  }
  .inprocessPublication {
    padding: 6px;
    font-weight: 600;
    font-size: 12px;
  }
  .completePublication {
    padding: 6px;
    font-weight: 600;
    font-size: 12px;
  }
  .research-card-left > img {
    object-fit: cover;
    margin-right: 1rem;
    width: 6rem;
    height: 6rem;
    border-radius: 999px;
    padding: 15px;
  }
}

@media (max-width: 750px) {
  .inprocessPublication {
    padding: 5px;
    font-weight: 600;
    font-size: 10px;
  }
  .completePublication {
    padding: 5px;
    font-weight: 600;
    font-size: 10px;
  }
  .research-title-name-duration {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-right: 10px;
  }
  .research-text-company {
    font-size: 18px;
    line-height: 32px;
  }  
  .research-text-desc {
    font-size: 15px;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    font-weight: 800;
  }
  .research-text-subHeader {
    padding-top: 0.5rem;
    font-size: 14px;
  }
  .research-card {
    display: flex;
    flex-direction: column;
    width: 90%;
    padding: 5%;
  }
  .research-card-right {
    width: 100%;
  }
  .research-card-border {
    width: 100%;
  }
}
